.quickSearchContainer{
    margin-left:30px;
    margin-top:50px;
}

.quicksearchheading{
    text-align: left;
    color: #192f60;
    font-size:40px;
    line-height: 1.0;
    font-weight: bold;
    font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
    margin-top: 50px;
    width: 24px;
}

.quickSearchSubHeading{
    text-align: left;
    color: gray;
    line-height: .08;
    letter-spacing: normal;
    font-size:20px;
    font-weight: normal;
    font-family:Georgia, 'Times New Roman', Times, serif;
    margin-top: 20px;
}


.tileContainer{
    box-shadow: 0 3px 6px black;
    margin-right: 80px;
    margin-bottom: 30px;
    width:350px;
    height: 150px;
    background-color: white;
    display: inline-block;
}
.tileComponent1{ 
    float: left;
    width:50%;
}

.tileComponent1>img{ 
    height:150px;
     Width:160px;

}
.tileComponent2{
    float: left;
    width:50%;
    margin-top: 20px;
    
}

.componentheading{
    text-align: left;
    color:#192f60;
    font-size:18px;
    font-weight: bold;

}

.componentsubheading{
    text-align: left;
    color:gray;
    font-size:14px;
    
}


.imageContainer{
    background: url('https://c.pxhere.com/photos/88/28/chefs_figures_funny_cook_restaurant_gastronomy_cute_chef_s_hat-494772.jpg!d');
    border-style: double;
    background-repeat: no-repeat;
    background-size: cover;
}
body{
        margin:auto;
    }

#logo{
    text-align: center;
    font-weight: 1000;
    color: rgb(221, 190, 199);
